@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('https://source.unsplash.com/1600x900/?landscape')
}

p {
  font-size: 1.6em;
}

h1 {
  font-size: 6em;
}

.app {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: white;
}

.app:before {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.search {
  text-align: center;
  padding: 1em;
}

.app input {
  padding: 0.7em 1.5em;
  font-size: 1.2em;
  border-radius: 25px;
  border: rgba(255,255,255,0.8);
  background: rgba(255,255,255,0.1);
  color: white;
}

::placeholder {
  color: rgba(255,255,255,0.3);
}

.container {
  max-width: 700px;
  height: 400px;
  margin: auto;
  padding: 0 1em;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1em auto;
}

.app .description {
  position: relative;
  left: 90%;
  transform-origin: 0 0;
  transform: rotate(-90deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  padding: 1em;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.2);
}

.bold {
  font-weight: 700;
}

.spinner {
  display: flex;
  justify-content: center;
}